      title1: "천연 반려동물 아로마 실내 방향제",
      title2: "판매가 : 280,000",
    };
  },
  methods: {
    showBox(boxNumber) {
      switch (boxNumber) {
        case 1:
          this.showResultBox1 = true;
          break;
        case 2:
          this.showResultBox2 = true;
          break;
        case 3:
          this.showResultBox3 = true;
          break;
        case 4:
          this.showResultBox4 = true;
          break;
        case 5:
          this.showResultBox5 = true;
          break;
      }
    },
    incrementQuantity1() {
      this.quantityOne++;
      this.priceOne = this.quantityOne * 280000 + "원";
    },
    decrementQuantity1() {
      if (this.quantityOne > 0) {
        this.quantityOne--;
        this.priceOne = this.quantityOne * 280000;
      }
    },
    incrementQuantity2() {
      this.quantityTow++;
      this.priceTow = this.quantityTow * 280000;
    },
    decrementQuantity2() {
      if (this.quantityTow > 0) {
        this.quantityTow--;
        this.priceTow = this.quantityTow * 280000;
      }
    },
    incrementQuantity3() {
      this.quantityThree++;
      this.priceThree = this.quantityThree * 280000;
    },
    decrementQuantity3() {
      if (this.quantityThree > 0) {
        this.quantityThree--;
        this.priceThree = this.quantityThree * 280000;
      }
    },
    incrementQuantity4() {
      this.quantityFour++;
      this.priceFour = this.quantityFour * 280000;
    },
    decrementQuantity4() {
      if (this.quantityFour > 0) {
        this.quantityFour--;
        this.priceFour = this.quantityFour * 280000;
      }
    },
    incrementQuantity5() {
      this.quantityFive++;
      this.priceFive = this.quantityFive * 280000;
    },
    decrementQuantity5() {
      if (this.quantityFive > 0) {
        this.quantityFive--;
        this.priceFive = this.quantityFive * 280000;
      }
    },
    totalPrice() {
      this.quantityOne * 280000 +
        this.quantityTwo * 280000 +
        this.quantityThree * 280000 +
        this.quantityFour * 280000 +
        this.quantityFive * 280000;
    },
  },
};
</script>

